import { ModelClient } from '@adobe/aem-spa-page-model-manager';
import auth from '@adobe/jwt-auth';

 export class RemoteSpaModelClient extends ModelClient {
     constructor(aemHost, authorization, routeToAemPathFn) {
         super(aemHost);
         this.authorization = authorization || '';
         this.routeToAemPathFn = routeToAemPathFn || function(route) { return route; };
     }

     async fetch(path) {
         console.log("Fetching path: " + path);
         if (!path) {
             return Promise.reject(new Error('Rejected fetching empty path.'));
         }
         const url = `${this.apiHost}${this.routeToAemPathFn(path)}`;
         const headers = await this._getHeaders(this.authorization);

         console.log("fetch URL: " + url);

         return fetch(
             url, 
             {
                headers: headers 
             }).then((response) => {
                 if ((response.status >= 200) && (response.status < 300)) {
                     return this._fixUrls(response.json());
                 } else {
                    return Promise.reject(response) 
                 }
             }).catch((error) => {
                 return Promise.reject(error);
             });
     }

     async _getHeaders(auth) {
        let headers = new Headers();

        headers.append('Content-Type', 'application/json');

        if (auth?.credentials) {            
            const accessToken =  await this._getAccessToken(auth.credentials);
            headers.append('Authorization', 'Bearer ' + accessToken);
        } else if (auth?.devToken) {
            headers.append('Authorization', 'Bearer ' + auth.devToken);
        } else if (auth?.basic) {
            const user = this.authorization.basic.user;
            const password = this.authorization.basic.password;

            headers.append('Authorization', 'Basic ' + btoa(user + ':' + password));
        } 

        return headers;
     }

     async _getAccessToken(credentials) {

        // This is the Service Credentials JSON object that must be exchanged with Adobe IMS for an access token
        let serviceCredentials = credentials.integration;

        // Use the @adobe/jwt-auth library to pass the service credentials generated a JWT and exchange that with Adobe IMS for an access token.
        // If other programming languages are used, please see these code samples: https://www.adobe.io/authentication/auth-methods.html#!AdobeDocs/adobeio-auth/master/JWT/samples/samples.md
        let { access_token } = await auth({
            clientId: serviceCredentials.technicalAccount.clientId, // Client Id
            technicalAccountId: serviceCredentials.id,              // Technical Account Id
            orgId: serviceCredentials.org,                          // Adobe IMS Org Id
            clientSecret: serviceCredentials.technicalAccount.clientSecret, // Client Secret
            privateKey: serviceCredentials.privateKey,              // Private Key to sign the JWT
            metaScopes: serviceCredentials.metascopes.split(','),   // Meta Scopes defining level of access the access token should provide
            ims: `https://${serviceCredentials.imsEndpoint}`,       // IMS endpoint used to obtain the access token from
        });

        return access_token;
    }

    async _fixUrls(json) {
        let updated = this._walkModel(await json);
        console.log(updated);
        return updated;
    }

    _walkModel(jsonObject) {
        let that = this;

        Object.keys(jsonObject).forEach(function (propertyName) {    
            if (jsonObject[propertyName] !== null && typeof jsonObject[propertyName] === 'object') {
                that._walkModel(jsonObject[propertyName]);
                return;
            }
            if (typeof jsonObject[propertyName] === 'string' && 
                    jsonObject[propertyName].match(/^\/content\/([^\0]*)(\.jpg|[^\0]jpeg|[^\0]png)$/i)) {
                jsonObject[propertyName] = that.apiHost + jsonObject[propertyName];
            }
        });
        return jsonObject;
    }
}
