import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
//import reportWebVitals from "./reportWebVitals";

import { ModelManager } from "@adobe/aem-spa-page-model-manager";
import "./index.css";

//import { contentPath } from "./utils";
//import * as serviceWorker from "./serviceWorkerRegistration"
import { RemoteSpaModelClient } from "./utils/RemoteSpaModelClient";

// Initialize the ModelManager before invoking ReactDOM.render(...).

const routeToModelPath = function(route) {
    if (route === '/index') {
        return '/content/wknd-app/us/en/index';
    }  

    return route;
}

const aemAuthorization = {
    basic: {
        user: 'admin',
        password: 'admin'
    }
};

ModelManager.initializeAsync({
    modelClient: new RemoteSpaModelClient(
        'http://localhost:4502',
        aemAuthorization,
        routeToModelPath
    ),
    // path: '/content/wknd-app/us/en/home'
});

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);

